.competition__wrapper-title,
.competition-form__user,
.competition-form__achievements,
.competition-form__contacts,
.competition-form__apps,
.competition-form__emotion {
  width: 320px;
  margin: 0 auto;
}

.competition__wrapper-title {
  padding: 46px 15px 40px;

  text-align: center;
}

.competition-form__wrapper {
  background-color: #f2f2f2;
}

.competition__title {
  margin: 0;

  font-size: 1.4rem;
  font-weight: 100;
  color: $darkBlue;
  line-height: 2.2;
}

.competition__form {
  display: flex;
  flex-direction: column;
}

.competition-form__user {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
}

.form-user__label {
  margin-left: 23px;
  margin-bottom: 13px;

  font-size: 1.4rem;
  text-transform: uppercase;
  color: #d22856;
}

.form-user__input {
  margin-bottom: 22px;
  padding: 20px;
  width: 280px;

  border: 2px solid #f2f2f2;

  outline: none;

  &::placeholder {
    color: $black;
  }

  &:last-of-type {
    margin-bottom: 43px;
  }

  &:hover {
    border-color: $black;
  }

  &:focus {
    border-color: #d22856;
  }
}

.competition-form__achievements {
  padding: 37px 31px 0;

  border: none;

  [type="checkbox"] {
    display: none;
  }
}

.form-achievements__title,
.form-contacts__title,
.form-apps__title,
.form-emotion__title {
  display: none;
}

.form-achievements__title--mobile {
  margin: 0;
  margin-bottom: 48px;

  font-size: 1.4rem;
  font-weight: 100;
  color: #d22856;
  text-transform: uppercase;
  line-height: 1.4;
  text-align: center;
}

.form-achievements__checkbox {
  position: relative;

  display: block;
  margin-bottom: 63px;
  margin-left: 58px;

  color: $darkBlue;

  cursor: pointer;

  &:last-of-type {
    margin-bottom: 52px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -10px;
    left: -69px;

    width: 39px;
    height: 39px;

    background-color: $white;
    border: 2px solid #e5e5e5;
  }
}

.form-achievements__checkbox:hover,
.form-achievements__checkbox:focus {
  &::before {
    border-color: $darkBlue;
  }
}

.form-achievements__checkbox:active {
  &::before {
    border-color: #d22856;
  }
}

input:checked + .form-achievements__checkbox::after {
  content: "";
  position: absolute;
  left: -60px;

  width: 22px;
  height: 16px;

  background-image: url(../img/tick.png);
  background-repeat: no-repeat;
}

.competition-form__contacts {
  display: flex;
  flex-direction: column;
  padding: 39px 20px;

  border: none;
}

.form-contacts__label {
  margin-bottom: 12px;
  margin-left: 22px;

  font-size: 1.4rem;
  color: #d22856;
  text-transform: uppercase;
}

.form-contacts__input {
  padding: 20px;
  margin-bottom: 24px;
  width: 280px;

  border: 2px solid #f2f2f2;

  outline: none;

  &::placeholder {
    color: $black;
  }

  &:last-of-type {
    margin-bottom: 3px;
  }

  &:hover {
    border-color: $black;
  }

  &:focus {
    border-color: #d22856;
  }
}

.competition-form__apps {
  display: flex;
  flex-direction: column;

  padding: 36px 40px 0;

  border: none;

  [type="radio"] {
    display: none;
  }
}

.form-apps__title--mobile {
  margin: 0;
  margin-bottom: 46px;

  font-size: 1.4rem;
  font-weight: 100;
  color: #d22856;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.4;
}

.form-apps__radio {
  position: relative;

  display: block;
  margin-left: 50px;
  margin-bottom: 56px;

  color: $darkBlue;

  cursor: pointer;

  &:last-of-type {
    margin-bottom: 51px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -11px;
    left: -70px;

    width: 39px;
    height: 39px;

    background-color: $white;
    border: 2px solid #e5e5e5;
    border-radius: 50%;
  }
}

.form-apps__radio:hover,
.form-apps__radio:focus {
  &::before {
    border-color: $darkBlue;
  }
}

.form-apps__radio:active {
  &::before {
    border-color: #d22856;
  }
}

input:checked + .form-apps__radio::after {
  content: "";
  position: absolute;
  top: 1px;
  left: -58px;

  width: 15px;
  height: 15px;

  background-color: #d22856;
  border-radius: 50%;
}

.competition-form__emotion {
  padding: 40px 20px 45px;
  border: none;
}

.form-emotion__title--mobile {
  margin: 0;
  margin-bottom: 18px;

  font-size: 1.4rem;
  font-weight: 100;
  color: #d22856;
  text-transform: uppercase;
  text-align: center;
}

.form-emotion__text {
  padding: 12px 19px;
  width: 280px;
  min-height: 200px;

  line-height: 1.7;

  border: 2px solid #f2f2f2;

  resize: none;
  outline: none;

  &::placeholder {
    color: $black;
  }

  &:hover {
    border-color: $black;
  }

  &:focus {
    border-color: #d22856;
  }
}

.competition-form__wrapper-button {
  padding: 0 20px 24px;
  width: 320px;
  margin: 0 auto;
}

.btn {
  width: 280px;
  padding: 21px 0;

  font-weight: bold;
  color: $white;
  text-transform: uppercase;
  letter-spacing: 0.7px;

  background-color: #2aab6d;
  border: none;

  cursor: pointer;
  outline: none;

  &:hover {
    background-color: #1c9b5e;
  }

  &:focus {
    color: #61b98f;

    background-color: #1c9b5e;
  }
}

.competition-form__button {
  margin-bottom: 43px;
}

.competition-form__note {
  margin: 0;

  font-size: 1.4rem;
  text-align: center;
  text-transform: uppercase;
}

.competition-form__note span {
  color: #d22856;
}