.popup-error,
.popup-sent {
  position: fixed;
  top: 25%;
  left: 50%;

  display: none;

  width: 320px;

  background-color: $white;
  box-shadow: 0 10px 25px -10px $black;

  transform: translateX(-50%);
}

@keyframes bounce {
  0% {transform: translateY(150%);}
  70% {transform: translateY(-10%);}
  85% {transform: translateY(5%);}
  100% {transform: translateY(0);}
}

.popup-open {
  display: block;

  animation-name: bounce;
  animation-duration: 0.5s;
}

.popup-error {
  padding: 40px 20px;
}

.popup-sent__wrapper {
  padding: 40px 26px 0;
}

.poput-sent__wrapper-button {
  padding: 50px 20px;

  background-color: #f2f2f2;
}

.popup__title,
.popup__description {
  margin: 0;
  padding: 0;
  
  text-align: center;
}

.popup__title {
  margin-bottom: 40px;
}

.popup__description {
  margin-bottom: 40px;

  font-size: 1.4rem;
  line-height: 1.6;
}